import React from 'react'

import { TaskList,NewTask, TagList } from './components'


class Tasks extends React.Component {

	constructor(){
		super()
	}

	render() {
		return (
			<div>
				<TagList />
				<NewTask />
			<TaskList tasks={[]} />
			</div>
		)
	}
}


export default Tasks



